.newsItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  padding-left: 200px;
  padding-right: 200px;
  background-color: #f7f7f7;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.newsTitle {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}
.newsText {
  font-size: 18px;
  color: #666;
  margin-bottom: 20px;
  text-align: justify;
  
}
iframe {
  width: 100%;
  height: 500px;
  border: none;
  margin-bottom: 20px;
}

